@import "./../../../styles/mixins.scss";
@import "./../../../styles/variables.scss";

.user-to-add__wrapper {
  @include containerShadow;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 10px 0;
  padding: 10px;

  @media (min-width: 768px) {
    width: 70%;
  }

  @media (min-width: 1024px) {
    width: 50%;
  }
}

.user-to-add__image {
  @include imageBase(50px, 70px, 70px, 70px);
}

.user-to-add__name {
  flex-grow: 1;
  max-width: 45%;
  font-weight: bold;
  color: white;
  overflow-wrap: break-word;

  @media (min-width: 768px) {
    font-size: 22px;
  }
}

.user-to-add__icon {
  padding-right: 5px;
  font-size: 30px;
  color: $blueWithOpacity;
  transition: 0.2s;

  @media (min-width: 768px) {
    font-size: 35px;
  }

  @media (min-width: 1024px) {
    cursor: pointer;
  }
}

.user-to-add__icon--delete {
  color: darkred;
}

@media (min-width: 1024px) {
  .user-to-add__icon--delete:hover {
    text-shadow: 0 0 10px darkred;
  }
}

.user-to-add__icon--add {
  color: $basicGreenColor;
}

@media (min-width: 1024px) {
  .user-to-add__icon--add:hover {
    text-shadow: 0 0 10px $basicGreenColor;
  }
}

